import React,{Component} from 'react'


class AddItems extends Component{
   
    state={
        name:'',
        description:''
    }
handleChange=(e)=>{
        this.setState({
        [e.target.id]:e.target.value
    })
}

handleSubmit=(e)=>{
e.preventDefault();

if(e.target.name.value===''|| e.target.description.value===''){
    return false
}else{
    this.props.sendItem('/',{}).then(data=>{
        this.props.addItem(this.state)
        this.setState({
            name:'',
            description:''
        })
    }).catch(error=>{
        console.log(error)
    })

}
}

    render(){
        return(
            <div>
                <form >
                    <input type="text" placeholder="Enter Name..." id="name" onChange={this.handleChange} value = {this.state.name}/>
                    <input type="text" placeholder="Enter description.." id="description" onChange={this.handleChange}
                        value= {this.state.description}
                    />
                    <button onClick={this.handleSubmit}  value = "add"/>
                </form>
            </div>
        )
    }


}
export default AddItems;